import React, { useState } from 'react'
import Toolbar from '../toolbar'

/* STYLE DO PRZEBUDOWANIA */
import '../../styles'

const App = () => {

    const [fontSize, setFontSize] = useState(100);
    const [contrast, setContrast] = useState(false);

    return (
        <React.Fragment>
            <Toolbar fontSize={fontSize} setFontSize={setFontSize} contrast={contrast} setContrast={setContrast} />
        </React.Fragment>
    )
};

export default App